import React from 'react';
import {ProductContext} from '../../context/ProductProvider'
import Order from './Order';
import Loading from '../Loader/Loading';
const OrdersList = () => {
    const {orders, loading} = React.useContext(ProductContext)
    console.log("orders wishlist::",orders.length)
    if(orders && orders.length && !loading){
        return (
            <section className="container">
                <section className="row">
                    {orders.map(order => <Order orderslist={order} key={order.orderDetails.productId}/>)}
                </section>
            </section>
        );
    }else{
       return <Loading/>
    }
    
};

export default OrdersList;