import React from 'react';
import './Cart.css'
import {CartContext} from '../../context/CartProvider'
import Cart from './Cart';
import {Link} from 'react-router-dom'
const CartList = () => {
    const {cartlist,cartTotal,createOrder} = React.useContext(CartContext)
    return (
        <>
        <section className="container py-5">
            <section className="row cart-headsection m-0">
                <section className="col-lg-9 col-sm-8 col-md-8 py-4 mx-auto">
                    <section className="card">
                        <section className="card-header prod-headcart">
                            <span><h5 className="cart-text">your cart details</h5></span>
                            <span><h5>total: {cartTotal}</h5></span>
                            <span><Link to="/review" onClick={createOrder} className="procedbtn">proceed</Link></span>
                        </section>
                    </section>        
                </section>
            </section>
            <section className="container itemcontainer">
                <section className="row">
                    {cartlist.map(list => <Cart cartList={list} key={list.product.productId}/>)}  
                </section>
            </section>
        </section>
        </>
    );
};

export default CartList;