import React from 'react';
import './Header.css'
import {MdNotifications, MdNotificationsActive} from 'react-icons/md'
import {FiSearch,FiMenu} from 'react-icons/fi'
import {Link} from 'react-router-dom'
import {ProductContext} from '../../context/ProductProvider'
import NavDrawer from '../Navigation/NavDrawer';
import BackDrop from '../Navigation/BackDrop';
const Header = () => {
    const notifications = ['hello'];
    let ntfView = <MdNotifications className="app_icon"/>;
    if(notifications.length > 0){
        ntfView = <div><MdNotificationsActive className="app_icon"/><div><span className="app_notifications">1</span></div></div>
    }
    const {getProducts,search,productFilter,navbarHandler,navbar} = React.useContext(ProductContext)
    return (
        <>
        <section className="app_header">
            <section className="logo_section">
                <div><FiMenu className="app_icon" onClick={navbarHandler}/></div>
                <div><span className="text-title_head app_name">Fresh Fare</span></div>
                <div>
                    {ntfView}
                    
                </div>
            </section>
            <section className="app_menu">
                <section className="list_items">
                    <ul>
                        <li><Link to="/">Home</Link></li>
                        <li><Link to="/products" onClick={() => getProducts('all')}>Products</Link></li>
                        <li><Link to="/about">About Us</Link></li>
                        <li><Link to="/cart">Cart</Link></li>
                        <li><Link to="/orders">Orders</Link></li>
                        <li><Link to="/wish">Wish List</Link></li>
                        <li><Link to="/help">Help</Link></li>
                        <li><Link to="/login">Account</Link></li>
                    </ul>
                </section>
                <section className="seach_icon">
                    <section><FiSearch className="app_search"/></section>
                    <section><input type="text" className="search_text" value={search} onChange={productFilter}/></section>
                </section>
            </section>
            <section className="mobile_search">
                <section className="seach_icon">
                    <section><FiSearch className="app_search"/></section>
                    <section><input type="text" className="search_text" values={search} onChange={productFilter}/></section>
                </section>
            </section>
        </section>
        <BackDrop show={navbar}/>
        <NavDrawer/>
        </>
    );
};

export default Header;