import React from 'react';
import './BannerProducts.css'
import tomoto from '../../asets/tomoto.jpg'
import {FaRegEye,FaRegHeart,FaCartPlus} from 'react-icons/fa'
import {ProductContext} from '../../context/ProductProvider'
import {CartContext} from '../../context/CartProvider'
import {Link} from 'react-router-dom'
const BannerProducts = ({products}) => {
    const {singleProduct} = React.useContext(ProductContext)
    const {addToWishList,addToCart,wishList} = React.useContext(CartContext)
    return (
        <section className="col-lg-2 col-sm-4 col-md-3 col-6 p-2">
            <section className="card product-card">
                <section className="img-container">
                    <img src={products.productImage} className="card-img-top" alt="product" style={{height: "120px", width: '120px'}}/>
                </section>
                <section className="p-2 prod-name">
                    <p className="mb-0">{products.productName}</p>
                </section>
                <section className="purchase-price">
                    <section className="prod-purchaseprice"><strike className="stike-price">₹{products.sellingPrice}</strike></section>
                </section>
                <section className="proddetails">
                    <span>{wishList.map(item => item.productId === products.productId) ? <FaRegHeart className="prod-icon" onClick={() => addToWishList(products.productId)}/> : "test" }</span>
                    <span className="prod-price"><p>₹{products.sellingPrice}</p></span>
                    <span><FaCartPlus className="prod-icon" onClick={() => addToCart(products.productId)}/></span>
                </section>
                <section className="view_products">
                    <Link to={`/products/${products.productId}`} className="eye_icon" onClick={() => {singleProduct(products.productId)}}><FaRegEye className=""/></Link>
                </section>
                {products.quantity <= 0 &&
                <section className="sold-out">
                    <span className="soldout-text">sold out</span>
                </section>}
            </section>
        </section>
    );
};

export default BannerProducts;