import React from 'react';
import './OffersDay.css'
import {ProductContext} from '../../../context/ProductProvider'
import Title from '../../Title/Title';
import BannerProducts from '../../BannerProducts/BannerProducts';
import Loading from '../../Loader/Loading';
const OffersDay = () => {
    const {dayProducts,loading} = React.useContext(ProductContext)
    if(loading){
        return <Loading/>
    }else{
    return (
        <section className="app_dayoffers">
            <Title link='' name="products of day" />
            <section className="container dayprod-container">
                <section className="row">
                    {dayProducts.map(items => <BannerProducts products={items} key={items.productId}/>)}
                </section>
            </section>
        </section>
    );
}
};

export default OffersDay;