import React from 'react';
import axios from 'axios'
import {FlattenProducts,PageNationProducts} from '../utils/ProductUtil'
import {productsUrl,productsByCategory,productById,ordersListUrl} from '../utils/static'
export const ProductContext = React.createContext()
const ProductProvider = ({children}) => {
    const [loading, setLoading] = React.useState(false);
    const [storeproducts, setStoreProducts] = React.useState([])
    const [filterProducts, setFilterProducts] = React.useState([])
    const [singleProductDetails, setSingleProduct] = React.useState(() => {
    let product = localStorage.getItem('product')
    return product ? JSON.parse(product) : []
    })
    const [shopProducts, setShopProducts] = React.useState([])
    const [dayProducts, setDayProducts] = React.useState([])
    const [pageNumber, setPageNumber] = React.useState(0)
    const [search, setSearch] = React.useState('')
    const [orders, setOrders] = React.useState([])
    const [navbar, setNavBar] = React.useState(false)

    React.useEffect(() => {
        getProducts("all")
        localStorage.setItem('product',JSON.stringify(singleProductDetails))
        getOrdersList()
        return () => {};
      }, [singleProductDetails]);

    const changePageValue = value => {
        setPageNumber(value)
    }

    const getProducts = id => {
        setLoading(true)
        setSearch('')
        if(id == "all"){
            axios.get(`${productsUrl}`+id).then(res =>{
                setStoreProducts(PageNationProducts(res.data))
                setFilterProducts(res.data)
                setShopProducts(FlattenProducts(res.data))
                setDayProducts(FlattenProducts(res.data))
                setLoading(false)
            }).catch(error => console.log(error))

        }else{
            console.log("product id:"+ id)
            axios.get(`${productsByCategory}`+id).then(res => {
                setFilterProducts(res.data)
                setLoading(false)
            }).catch(err => {console.log("error:"+err)})
        }
    }

    const singleProduct = (id) => {
        setLoading(true)
        console.log("product id:"+id)
        console.log("before singleProduct method")
        axios.get(`${productById}`+id).then(
            res =>{ setSingleProduct(res.data)
                setLoading(false)
        }).catch(err=> console.log(err))
    }
    
    const productFilter = event => {
        setSearch(event.target.value)
    }

    React.useLayoutEffect(()=>{
        let products = [...filterProducts]
        if(search !== ''){
            products = products.filter(item => {
                let productName = item.productName.toLowerCase().trim();
                return productName.startsWith(search) ? item : null
            })
        }
        setPageNumber(0)
        setStoreProducts(PageNationProducts(products))
    },[search, filterProducts])

    const navbarHandler = () =>{
        let navOpen = navbar
        setNavBar(!navOpen)
        console.log("nav open")
    }
    const closeNavBar = () => {
        setNavBar(false)
    }
    const getOrdersList = () => {
        setLoading(true)
        axios.get(`${ordersListUrl}vamshider007@gmail.com`).then(
            res => {
                setOrders(res.data)
                setLoading(false)
            }
        ).catch(err=>{

        })
    } 
    return (
        <ProductContext.Provider value={{filterProducts,shopProducts,
            dayProducts,getProducts,storeproducts,
            singleProductDetails,singleProduct,pageNumber,changePageValue,
            loading, search, productFilter,navbar,navbarHandler,closeNavBar,orders}}>
            {children}
        </ProductContext.Provider>
    );
};

export default ProductProvider;