import React from 'react';
import './BannerCatlogue.css'
import {useHistory} from 'react-router-dom'
import {ProductContext} from '../../context/ProductProvider'
const BannerCatlogue = ({products}) => {
    const {getProducts} = React.useContext(ProductContext)
    const history = useHistory()
    return (
        <section className="col-lg-2 col-sm-4 col-md-3 col-6 py-2">
            <div className="card">
                <div className="img-container">
                    <img src={products.categoryImage} className="card-img-top" alt="product" style={{height: "150px"}}/>
                </div>
                <div className="d-flex p-2 justify-content-between">
                    <p className="mb-0">{products.category}</p>
                </div>
                <button to="/products" onClick={() => {getProducts(products.categoryId); history.push('/products');}}>View All</button>
            </div>
        </section>
    );
};

export default BannerCatlogue;