import React from 'react';
import {ProductContext} from '../../context/ProductProvider'
import {CartContext} from '../../context/CartProvider'
import {Link} from 'react-router-dom'
import './product.css'
import Loading from '../Loader/Loading';
const SingleProduct = () => {
    const {singleProductDetails,singleLocalStorage, loading,singleProduct} = React.useContext(ProductContext)
    const {addToCart,addToWishList} = React.useContext(CartContext)
    const {productId, productName, purchasePrice, sellingPrice, productImage, description} = singleProductDetails
    if(loading){
        return <Loading/>
    }else{ 
        return (
            <section className="container">
                <section className="row product-container">
                    <section className="col-12">
                        <section className="card">
                            <section className="card-body">
                                <section className="container">
                                    <section className="row">
                                        <section className="col-lg-4 col-md-8 col-sm-8">
                                            <img src={productImage} className="product-image"/>
                                        </section>
                                        <section className="col-lg-8 col-md-8 col-sm-8">
                                            <section className="prod-name"><span><p>product name: {productName}</p></span></section>
                                            <section className="prod-price"><span><p> selling price: <strike>{purchasePrice}</strike></p></span><span><p>{sellingPrice}</p></span></section>
                                        </section>
                                    </section>
                                </section>
                            </section>
                            <section className="card-footer">
                                <section className="prod-description"><p className="m-0">description: {description}</p></section>
                                <section className="prod-buttons">
                                    <input type="button" className="prod-btn" value="add to wishlist" onClick={addToWishList}/>
                                    <input type="button" className="prod-btn" value="add to cart" onClick={addToCart}/>
                                    <Link to="/products" className="prod-btn">back to products</Link>
                                </section>
                            </section>
                        </section>
                    </section>
                </section>
            </section>
    );
}
};

export default SingleProduct;