import React from 'react';
import {UserContext} from '../../context/UserProvider'
import {MdClose} from 'react-icons/md'
import './AlertModel.css'
import {useHistory} from 'react-router-dom'
const AlertModel = () => {
    const history = useHistory()
    const {model, closeModel} = React.useContext(UserContext)
    let alertshow = '';
    let alertBcg = '';
    if(model.show){
        console.log("true show")
        alertshow="alert_display"
        if(model.type === 'failure'){
            alertBcg="alert_bcg"
        }
    }else{
        console.log("false show")
    }
    return (
        <section className={`model_conatainer ${alertshow} ${alertBcg}`}>
            <section className="alert_model">
                <p>{model.message}</p>
                <button onClick={closeModel} className="model_close"><MdClose/></button>
            </section>
        </section>
    );
};

export default AlertModel;