import React from 'react';
import {ProductContext} from '../../context/ProductProvider'
import {Link} from 'react-router-dom'
const NavDrawer = () => {
    const {navbar,getProducts,closeNavBar} = React.useContext(ProductContext)
    return (
        <section className={`side-nav ${navbar ? 'open-nav' : 'close-nav'}`}>
            <section className="list_itemsnav">
                    <ul>
                        <li><Link to="/" onClick={closeNavBar}>Home</Link></li>
                        <li><Link to="/products" onClick={() => getProducts('all'),closeNavBar}>Products</Link></li>
                        <li><Link to="/about" onClick={closeNavBar}>About Us</Link></li>
                        <li><Link to="/cart" onClick={closeNavBar}>Cart</Link></li>
                        <li><Link to="/orders" onClick={closeNavBar}>Orders</Link></li>
                        <li><Link to="/wish" onClick={closeNavBar}>Wish List</Link></li>
                        <li><Link to="/help" onClick={closeNavBar}>Help</Link></li>
                        <li><Link to="/login" onClick={closeNavBar}>Account</Link></li>
                    </ul>
                </section>
        </section>
    );
};

export default NavDrawer;