import React from 'react';
import {CartContext} from '../context/CartProvider'
import {Link} from 'react-router-dom'
const ReviewPage = () => {
    const {cartlist,cartTotal,confirmOrder} = React.useContext(CartContext)
    
    return (
        <section className="review-head">
            <section className="container py-5">
                <section className ="row">
                    <section className="col d-flex py-4 justify-content-between">
                        <h3>Confirm Order</h3>
                        <h3>Toatl: {cartTotal}</h3>
                        <Link to="" onClick={confirmOrder}>Submit</Link>
                    </section>
                </section>
            </section>
        </section>
    );
};

export default ReviewPage;