export function FlattenProducts(response){
    return response.slice(0, 6);
}

export function FlattenCategories(response){
    return response.slice(0, 6);
}

export function PageNationProducts(products){

    const pageItems = 18;
    const numberOfpages = Math.ceil(products.length/pageItems)
    const pageProducts = Array.from({length: numberOfpages}, (_,index) => {
        const startPage = index * pageItems
        return products.slice(startPage, startPage + pageItems)
    })
    return pageProducts
}

