import React from 'react';
import {CategoryContext} from '../context/CategoryProvicer'
import BannerCatlogue from '../components/BannerCatlogue/BannerCatlogue';
const Catalogues = () => {
    const {catalogues} = React.useContext(CategoryContext)
    return (
        <section className="all_catalogues">
            <section className="container">
                <section className="row">
                    {catalogues.map(catItems => <BannerCatlogue products={catItems} key={catItems.categoryId}/>)}
                </section>
            </section>
        </section>
    );
};

export default Catalogues;