import React from 'react';
import axios from 'axios'
import {sideMenu} from '../utils/staticData'
import {categoryUrl} from '../utils/static'
import {FlattenCategories} from '../utils/ProductUtil'
export const CategoryContext = React.createContext();
const CategoryProvicer = ({children}) => {
    const [loading, setLoading] = React.useState(false);
    const [catalogues, setCatalogues] = React.useState([]);
    const [topCatalogues, setTopcatalogues] = React.useState([]);
    const [sideMenuList, setSideMenu] = React.useState([]);
    React.useEffect(() => {
        console.log("hello navaj category provider")
        
        getCategories()
        setSideMenu(sideMenu)
        
        
        return () => {};
      }, []);
      const getCategories = () => {
        setLoading(true);
        axios.get(`${categoryUrl}`).then(result => {
            setCatalogues(result.data);
            setTopcatalogues(FlattenCategories(result.data));
            setLoading(false);
            console.log("top categories :: ", result.data)
        }).catch(err=> {
            console.log(`top categories err:${err}`);
        })
      }
    return (
        <CategoryContext.Provider value={{loading,sideMenuList,catalogues,topCatalogues}}>
            {children}
        </CategoryContext.Provider>
    );
};

export default CategoryProvicer;