import React from 'react';
import './Title.css'
import {Link} from 'react-router-dom'

const Title = ({name,link}) => {
    return (
        <section className="container">
            <section className="row">
                <section className="col text_titles">
                    <span className="text-title_head my-3">{name}</span>
                    <span className="my-3"><Link to={link} className="text-all_link">view all</Link></span>
                </section>
            </section>
        </section>
    );
};

export default Title;