import React from 'react';
import {FaAngleDoubleRight} from 'react-icons/fa'
import { Link } from 'react-router-dom';
const Order = ({orderslist}) => {
    return (
        <section className="col-lg-8 col-sm-8 col-md-8 mx-auto py-2">
            <section className="card">
                <section className="card-body">
                    <section className="seller-details">
                        <span><p>ord no: {orderslist.orderNumber}</p></span>
                        <span><p>total amt. 205</p></span>
                    </section>
                    <section className="orderitems-quanity">
                        <span><p className="order-date">ord date: {orderslist.issueDate}</p></span> 
                        <span><p className="order-date">delry date: {orderslist.deliveryDate}</p></span> 
                    </section>
                    <section className="delivery-status">
                        <span><p className="order-status">Status: {orderslist.delivered ? 'delivered': 'in progress'}</p></span> 
                    </section>
                </section>
                <section className="card-footer">
                    <section className="viewmore">
                        <span><p className="ord-items">Items. 12</p></span>
                        <span><Link to={{ pathname:"/orderitems" }}><FaAngleDoubleRight className="order-icon"/></Link></span>
                    </section>
                </section>
            </section>
        </section>
    );
};

export default Order;