import React from 'react';
import { Link } from 'react-router-dom';
const Wish = ({wishlistitem,singleProd}) => {
    return (
        <section className="col-lg-9 col-sm-8 col-md-8 py-2 mx-auto">
                <section className="card">
                    <section className="card-body">
                        <section className="wishlist-body">
                            <section className="container">
                                <section className="row">
                                    <section className="col-lg-4 col-sm-8 col-md-4 wish-image py-2">
                                        <img src={wishlistitem.product.productImage} className="wish_img"/>
                                    </section>
                                    <section className="col-lg-8 col-sm-8 col-md-8  item-details py-2">
                                        <span><p>product name: {wishlistitem.product.productName}</p></span>
                                        <span><p>purchase price: {wishlistitem.product.purchasePrice}</p></span>
                                        <span><p>selling price: {wishlistitem.product.sellingPrice}</p></span>
                                    </section>
                                </section>
                            </section>
                        </section>
                        <section className="card-footer">
                            <section className="prodwish-handle">
                                <section>  
                                    <input type="button" className="prodwish-btn remov-btn" value="remove"/>
                                </section>
                                <section> 
                                    <Link type="button" to={`/products/${wishlistitem.product.productId}`} onClick={() => {singleProd(wishlistitem.product.productId)}} className="prodwish-btn">view</Link>
                                </section>
                                <section> 
                                    <input type="button" className="prodwish-btn" value="add to cart"/>
                                </section>
                            </section>
                        </section>
                    </section>
                </section>
            </section>
    );
};

export default Wish;