import React from 'react';
import './App.css';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom'
import ErrorPage from './Pages/ErrorPage';
import AboutPage from './Pages/AboutPage';
import HomePage from './Pages/HomePage';
import CartPage from './Pages/CartPage';
import ProductsPage from './Pages/ProductsPage';
import ProductPage from './Pages/ProductPage';
import Header from './components/header/Header';
import Footer from './components/footer/Footer';
import Catalogues from './Pages/Catalogues';
import createHistory from 'history/createBrowserHistory'
import LoginPage from './Pages/LoginPage';
import AlertModel from './components/Model/AlertModel';
import WishList from './Pages/WishList';
import ReviewPage from './Pages/ReviewPage';
import OrdersPage from './Pages/OrdersPage'
import OrderItemsPage from './Pages/OrderItemsPage';
function App() {

  return (
    <Router>
      <Header/>
      <AlertModel/>
      <Switch>
        <Route exact path="/" component={HomePage}/>
        <Route path='/about' component={AboutPage}/>
        <Route path="/cart" component={CartPage}/>
        <Route path="/wish" component={WishList}/>
        <Route path="/products" exact component={ProductsPage}/>
        <Route path="/products/:id" component={ProductPage}/>
        <Route path="/catalogues" component={Catalogues}/>
        <Route path="/login" component={LoginPage}/>
        <Route path="/alert" component={AlertModel}/>
        <Route path="/review" component={ReviewPage}/>
        <Route path="/orders" component={OrdersPage}/>
        <Route path="/orderitems" component={OrderItemsPage}/>
        <Route path="*" component={ErrorPage}/>
      </Switch>
      <Footer/>
    </Router>
  );
}

export default App;
