import React from 'react'
import AllProducts from '../components/products/AllProducts'
import {ProductContext} from '../context/ProductProvider'
import Loading from '../components/Loader/Loading'
import {FaAngleLeft,FaAngleRight} from 'react-icons/fa'
const ProductsPage = () => {
    const {storeproducts,pageNumber,changePageValue,loading,search} = React.useContext(ProductContext)
    console.log("storeproducts :: ",storeproducts[pageNumber])
    if(storeproducts[pageNumber]){
         return (
             <section>
                <AllProducts productsAll={storeproducts[pageNumber]}/>
                <section className="container py-5">
                    <section className="row">
                        <section className="col d-flex justify-content-end">
                            <section className="pagbuttons">
                                {pageNumber > 0 &&  <span className="pageleft-right"><FaAngleLeft className="page-icon" onClick={() => changePageValue(pageNumber - 1)}/></span>}
                                <span>{storeproducts.map((_,index) => {
                                    return (
                                        <button className={`page-btn ${pageNumber === index && 'currentpage'}`} onClick={() => changePageValue(index)}>{index + 1}</button>
                                    )
                                })}</span>
                                {pageNumber < storeproducts.length - 1 && <span className="pageleft-right"><FaAngleRight className="page-icon" onClick={() => changePageValue(pageNumber + 1)}/></span>}
                            </section>
                        </section>
                    </section>
                </section>
             </section>
            
        );
    }else if(search !== '') {
        return (
          <section className="container">
              <section className="row">
                  <section className="col search-failed">
                    <span><h3>Items Not Found...</h3></span>
                  </section>
              </section>
          </section>
        );
      }else {
        return (
            <Loading/>
          );
      }
};

export default ProductsPage;