import React from 'react';
import {CartContext} from '../../context/CartProvider'
import {ProductContext} from '../../context/ProductProvider'
import Wish from './Wish';
const WishLists = () => {
    const {wishList} = React.useContext(CartContext)
    const {singleProduct} = React.useContext(ProductContext)
    return (
        <section className="container py-5">
            <section className="row">
                {wishList.map(list => <Wish wishlistitem={list} key={list.product.productId} singleProd={singleProduct}/>)}  
            </section> 
        </section>
    );
};

export default WishLists;