import React from 'react';
import {CategoryContext} from '../../context/CategoryProvicer'
import './Categories.css'
import Title from '../Title/Title';
import BannerCatlogue from '../BannerCatlogue/BannerCatlogue';
import Loading from '../Loader/Loading';
const Categories = () => {
    const {topCatalogues,loading} = React.useContext(CategoryContext);
    if(loading){
        return <Loading/>
    }else{
    return (
        <section className="app_catalogues">
            <Title link='/catalogues' name="popular categories"/>
            <section className="container">
                <section className="row">
                    {topCatalogues.map(product => <BannerCatlogue products={product} key={product.categoryId}/>)}
                </section>
            </section>
        </section>
    );
}
};

export default Categories;