import React from 'react';
import axios from 'axios'
import {domainUrl,registartionUrl} from '../../utils/static'
import './Login.css'
import {UserContext} from '../../context/UserProvider'
const Login = () => {
    const {model, showModel} = React.useContext(UserContext)
    const [username, setUserName] = React.useState('')
    const [password, setPassword] = React.useState('')
    const [regusername, serRegUser] = React.useState('')
    const [regpassword, setRegPass] = React.useState('')
    const [regmobile, setRegMobile] = React.useState('')
    const [isUser, setIsUser] = React.useState(true)

    let isLoginEampty = !username || !password;
    let isRegEampty = !regusername || !regpassword || !regmobile;

    const submitHandler = e => {
        if(isUser){
            console.log("hello login")
            showModel({
                message: "please wait..."
            })
            axios.get(`${domainUrl}/customer/login?customerId=${username}&password=${password}`)
            .then(res => {
                console.log("resoponse login" + res.data);
                if(res.data){
                    showModel({message: "you logged successfully"})
                }else{
                    showModel({message: "login failed",type: "failure"})
                }
                
            }).catch(error => {
                console.log("resoponse login" + error)
                showModel({message: "network issue",type: "failure"})
            })
        }else{
            console.log("hello register")
            let regBody = {
                "customerId": regusername,
                "firstName":"navaj",
	            "lastName":"reddy",
	            "genderId":1,
	            "dob":"1995-06-21",
	            "mobile":regmobile,
	            "email":regusername,
	            "alternateMobile":regmobile,
	            "address":{
		            "customerId":regusername,
		            "houseNumber":"3-122/7",
		            "street":"attapur",
		            "city":"hyderguda",
		            "district":"Hyderabad",
		            "locality":"temple",
		            "state":"Telangana",
		            "pincode":"500067"
	            },
	            "customerLogin":{
		            "customerId":regusername,
		            "password":regpassword
                },
                "customerCart": {
                    "customerId":regusername,
                    "productId": 0,
                    "quantity": 0
                },
                "customerWishlist":{
                    "customerId":regusername,
                    "productId":0
                }
            }
            axios.post(registartionUrl,regBody).then(res => {
                console.log("registration response::"+res.data)
                toggleHandler()
            }).catch(error => {
                console.log("registration error::"+error)
            })

        }
    }

    const toggleHandler = () =>{
        setIsUser( (prev)=> {
           let isUser = !prev
           return isUser
        })
        setUserName('')
        setPassword('')
        setRegMobile('')
        setRegPass('')
        serRegUser('')
    }
    let disable = true;
    if(!isLoginEampty || !isRegEampty){
        disable = false
    }

    return (
        <section className="container">
            <section className="row">
                <section className="col">
                    <section className="form-section">
                        <section className="app_forminner">
                        <h2 className="text-form_head">{isUser ? 'Sign in' : 'Register'}</h2>
                        <form className="app_form">
                        {isUser &&
                            <section className="app_login">
                                <section className="form-group">
                                    <label className="form_label">Email</label>
                                    <input type="email" className="form-control form_input" value={username} 
                                    onChange={e => setUserName(e.target.value)}/>
                                </section>
                                <section className="form-group">
                                    <label className="form_label">Password</label>
                                    <input type="password" className="form-control form_input" value={password} 
                                    onChange={e => setPassword(e.target.value)}/>
                                </section>
                            </section> }
                            { !isUser && 
                                <section className="app_register">
                                    <section className="form-group">
                                        <label className="form_label">Email*</label>
                                        <input type="email" className="form-control form_input" value={regusername} 
                                        onChange={e => serRegUser(e.target.value)} required/>
                                    </section>
                                    <section className="form-group">
                                        <label className="form_label">Password*</label>
                                        <input type="text" className="form-control form_input" value={regpassword}
                                        onChange={e => setRegPass(e.target.value)} required/>
                                    </section>
                                    <section className="form-group">
                                        <label className="form_label">Mobile no*</label>
                                        <input type="tel" className="form-control form_input" value={regmobile}
                                        onChange={e => setRegMobile(e.target.value)}/>
                                    </section>
                                </section>}
                           
                            <input type="button" className="btn btn-primary submit_button" 
                             value={isUser ? 'Login' : 'Register'} onClick={submitHandler} disabled={disable}/>
                        </form>
                        <section className="login_footer">
                            {isUser ? 'need to register ?' : 'already a memeber ?'}
                            <button onClick={toggleHandler}>Click here</button>
                        </section>
                        </section>
                    </section>
                </section>
            </section>
        </section>
    );
};

export default Login;