import React from 'react';
import Title from '../../Title/Title';
import {ProductContext} from '../../../context/ProductProvider'
import BannerProducts from '../../BannerProducts/BannerProducts';
import Loading from '../../Loader/Loading'
import './ShopOffers.css'
const ShopOffers = () => {
    const {shopProducts, loading} = React.useContext(ProductContext)
    if(loading){
        return <Loading/>
    }else{
    return (
        <section className="app_offers">
            <Title link='' name="offers by shop"/>
            <section className="container shopoferprod">
                <section className="row">
                    {shopProducts.map(items=> <BannerProducts products={items} key={items.productId}/>)}
                </section>
            </section>
        </section>
    );
}
};

export default ShopOffers;