import React from 'react';
import {FaPlusCircle,FaMinusCircle,FaRegTrashAlt} from 'react-icons/fa'
import {CartContext} from '../../context/CartProvider'
const Cart = ({cartList}) => {
    const {removeCartItem, addToCart} = React.useContext(CartContext)
    return (
            <section className="col-lg-9 col-sm-8 col-md-8 py-2 mx-auto">
                <section className="card">
                    <section className="card-body">
                        <section className="cart-body">
                            <section className="container">
                                <section className="row">
                                    <section className="col-lg-4 col-sm-8 col-md-4 cart-image py-2">
                                        <img src={cartList.product.productImage} className="cart_img"/>
                                    </section>
                                    <section className="col-lg-8 col-sm-8 col-md-8  item-details py-2">
                                        <span><p>product name: {cartList.product.productName}</p></span>
                                        <span><p>purchase price: {cartList.product.purchasePrice}</p></span>
                                        <span><p>selling price: {cartList.product.sellingPrice}</p></span>
                                    </section>
                                </section>
                            </section>
                        </section>
                        <section className="card-footer">
                            <section className="prod-handle">
                                <section><span><p>sub total : {cartList.quantity * cartList.product.sellingPrice}</p></span></section>
                                <section>
                                    <span><FaRegTrashAlt className="prod-remove" onClick = {() => removeCartItem(cartList.product.productId)}/></span>
                                </section>
                                <section>
                                    <span><FaMinusCircle className="cart-icon" onClick={() => addToCart(cartList.product.productId)}/></span>
                                    <span className="prod-quantity" >222</span>
                                    <span><FaPlusCircle className="cart-icon" onClick={() => addToCart(cartList.product.productId)}/></span>
                                </section>
                            </section>
                        </section>
                    </section>
                </section>
            </section>
    );
};

export default Cart;