import React from 'react';
import BannerProducts from '../BannerProducts/BannerProducts'
const AllProducts = ({productsAll, wish}) => {
    return (
        <section className="app_products">
            <section className="container">
                <section className="row products-container">
                        {productsAll.map(items => <BannerProducts products={items} key={items.productId}/>)}
                </section>
            </section>
        </section>
    );
};

export default AllProducts;