import React from 'react';
import axios from 'axios'
import {
    addToCartUrl, addToWishListUrl, getCartListUrl, 
    getWishListUrl, removeCartItemUrl,
    createOrderUrl,confirmOrderUrl} from '../utils/static'
export const CartContext = React.createContext()
const CartProvider = ({children}) => {
    const [cartlist, setCartList] = React.useState([])
    const [wishList, setWishList] = React.useState([])
    const [cartTotal, setCartTotal] = React.useState('')
    const [orderId, setOrderId] = React.useState('')
    const [loading,setLoading] = React.useState(false)
    const [cartLength, setCartLength] = React.useState('')
    React.useEffect(()=> {
        setLoading(true)
        getCartList()
        getWishList()
        return () => {};
    },[])
    React.useEffect( () => {
        cartTotals()
    },[cartlist,orderId])
    const getCartList = () => {
        setLoading(true)
        axios.get(getCartListUrl).then(res => {
            console.log("get cart response::"+res)
            setCartList(res.data)
            setLoading(false)
        }).catch(err => console.log("error get cart list::"+err))
    }
    const getWishList = () => {
        setLoading(true)
        axios.get(getWishListUrl).then(res => {
            console.log("get wishlist response::"+res)
            setWishList(res.data)
            setLoading(false)
        }).catch(err => console.log("error get cart list::"+err))
    }
    const addToCart = (id) => {
        setLoading(true)
        console.log("add to cart id::"+id)
        let data = {
            "productId": id,
            "quantity": 1
        }
        axios.put(addToCartUrl,data).then(res=> console.log("add to cart response::"+res),setLoading(false))
        .catch(err => console.log("error add to cart ::"+err))
    }
    const addToWishList = (id) =>{
        setLoading(true)
        console.log("add to wishlist id::"+id)
        let data = {
            "productId": id
        }
        axios.put(addToWishListUrl,data).then(res => { 
            console.log("add to wishlist response::"+res)
            setLoading(false)
        })
        .catch(err => console.log("error add to wishlist ::"+err))
    }
    const cartTotals = () => {
        console.log("cart total before: ")
        let total = getTotal()
        setCartTotal(total)
    }
    const getTotal = () => {
        let totals = 0;
        const cartlists = cartlist
        cartlist.map(item => {
            totals += item.quantity * item.product.sellingPrice
        })
        return totals
    }
    const removeCartItem = (id) => {
        setLoading(true)
        console.log("cart id :: "+ id);
        let proId= {
            "productId" : id
        }
        axios.put(removeCartItemUrl, proId).then(
            res=> getCartList(), cartTotals(),
            setLoading(false)
        ).catch( err => {
            console.log("error :: "+err)
        })
    }
    const createOrder = () => {
        setLoading(true)
        console.log("create order cart object ::" , cartlist)
        let orderObj = {
            "customerId":"vamshider007@gmail.com", 
            "issueDate":"2020-05-07", 
            "isDelivered":false, 
            "deliveryDate":"2020-05-20",
            "orderDetails":  cartlist.map(items =>{ 
                    return {
                    "productId":items.product.productId,
                    "quantity": items.quantity
                    }
                })
        }
        axios.post(createOrderUrl, orderObj).then(
            res => {
                setOrderId(res.data.orderNumber)
                setLoading(false)
            console.log("order id response ::"+ res.data.orderNumber)
        }
        ).catch( err => console.log("order error ::", err))
    }
    const confirmOrder = () => {
        setLoading(true)
       
    }
    return (
        <CartContext.Provider 
        value= {{cartlist,addToCart,addToWishList,
                wishList,getWishList,cartTotal,
                removeCartItem,createOrder,confirmOrder,
                cartLength}}>
            {children}
        </CartContext.Provider>
    );
};

export default CartProvider;